@charset "UTF-8";
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Noto+Sans+TC:wght@100;300;400;500;700;900&family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&family=Orbitron:wght@400;500;600;700;800;900&family=Prompt:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

/* === Universal setting ========================================== */
* {
  margin: 0;
  padding: 0;
  border: none;
}
body {
  display: flex;
  font-family: 'Prompt', 'Noto Sans TC';
  font-size: 16px;
  color: #333;
  background: #0f0f0f;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
ul,
ol {
  list-style: none;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  user-select: none;
}

#root {
  width: 100%;
}
#Tips {
  display: flex;
  align-items: center;
  position: fixed;
  width: calc(100% - 4rem);
  max-width: 480px;
  bottom: calc(60px + 1rem);
  padding: 0.5rem 1rem 0.5rem 1rem;
  background: #3636369a;
  margin-left: 2rem;
  box-shadow: 0 3px 20px 0 #000;
  border-radius: 0.7rem;
  -webkit-border-radius: 0.7rem;
  -moz-border-radius: 0.7rem;
  -ms-border-radius: 0.7rem;
  -o-border-radius: 0.7rem;
}
#Tips span {
  color: #e1e1e1;
  font-size: 1rem;
}

/* === Universal background setting ========================================== */
@supports ((-webkit-backdrop-filter: initial) or (backdrop-filter: initial)) {
  #Tips {
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
  }
}

.bg-blue-green {
  background: linear-gradient(150deg, #3e71c2, #42b883);
  box-shadow: 0 0 40px 0 inset #9f9f9fa1;
}
.bg-gray-deepgray {
  background: linear-gradient(150deg, #5f5f5f, #353535);
}
.bg-black {
  background: #0f0f0f !important;
}
.bg-purple-blue {
  background: linear-gradient(150deg, #7a43be, #515ebf);
  box-shadow: 0 0 40px 0 inset #9f9f9fa1;
}
.bg-red-orange {
  background: linear-gradient(150deg, #c23e3e, #b87142);
  box-shadow: 0 0 40px 0 inset #9f9f9fa1;
}
.bg-history {
  background: #000000;
  box-shadow: 0 0 40px 0 #1c1c1c;
}

/* === Universal style setting ========================================== */

main {
  position: absolute;
  width: 100%;
  height: calc(100vh - 108px);
  top: 48px;
  bottom: 48px;
  overflow: scroll;
}
.display-none {
  display: none;
}
.navSpace {
  height: 48px;
}

/* === Nav ========================================== */
nav {
  z-index: 1000;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  height: 48px;
  width: 100%;
  background-color: #1c1c1c;
}
#header {
  z-index: 1;
  display: flex;
  align-items: center;
  height: 48px;
  width: 100%;
  background: #1c1c1c;
  box-shadow: 0 5px 5px 0px #0f0f0f;
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
#header.open {
  box-shadow: 0 5px 5px 0px #1c1c1c;
}
#header-ul {
  display: flex;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}
#header-li {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  height: 100%;
  width: 48px;
}
#header-li img.icon {
  height: 32px;
  width: 32px;
}
#header-li button {
  z-index: 1;
  position: absolute;
  transform: translateX(-50%);
  left: calc(50% + 54px);
  font-size: 0.3rem;
  color: #a1a1a1;
  opacity: 0;
  background: #2f2f2f;
  padding: 0.2rem;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transition: 0.35s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.35s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.35s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.35s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.35s cubic-bezier(0.5, 0, 0.3, 1);
}
#header-li:hover > .flashBtn {
  opacity: 1;
  font-size: 0.5rem;
  padding: 0.4rem;
}
.flashBtn:hover {
  opacity: 1;
  font-size: 0.5rem;
  padding: 0.4rem;
}
.classdata-icon a {
  display: flex;
  align-items: center;
}

/* ======================= Menu ====================== */
menu {
  z-index: -1;
  position: fixed;
  height: 0;
  width: 100%;
  top: 48px;
  left: 0;
  opacity: 0;
  background: linear-gradient(180deg, #1c1c1c, #0f0f0f);
  pointer-events: none;
  transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  pointer-events: none;
}
menu.open {
  height: calc(100vh - 96px);
  opacity: 1;
  pointer-events: all;
}
#menu-view {
  display: block;
  margin-top: -60vh;
  width: 90%;
  margin-left: 5%;
  transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: 0.65s cubic-bezier(0.5, 0, 0.3, 1);
}
#menu-view.show {
  margin-top: 0;
  opacity: 1;
}
#menu-block {
  margin: 24px 0;
  width: 100%;
}
#menu-block span {
  font-size: 18px;
  color: #7e7e7e;
}
#menu-block ul {
  padding: 12px 0;
}
#menu-linkView li h1 {
  font-size: 18px;
  font-weight: 700;
  color: #fff;
}
#menu-linkView {
  display: flex;
  width: 100%;
}
.menu-link {
  width: calc(50% - 10px);
  margin: 5px;
  padding: 13px;
  background-position: top;
  background-size: cover;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
.filter {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.1);
}

.menuicon:hover > span {
  background-color: #e1e1e1;
  width: 19px;
  transition: 0.25s;
  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  -ms-transition: 0.25s;
  -o-transition: 0.25s;
}
/* === 菜單按鈕 === */
.menuicon-top {
  display: block;
  position: absolute;
  top: 20px;
  width: 17px;
  height: 1px;
  background: #ffffffaa;
  border-radius: 0.5px;
  -webkit-border-radius: 0.5px;
  -moz-border-radius: 0.5px;
  -ms-border-radius: 0.5px;
  -o-border-radius: 0.5px;
  transition: top 0.5s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.25s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: top 0.5s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.25s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: top 0.5s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.25s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: top 0.5s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.25s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: top 0.5s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.25s cubic-bezier(0.5, 0, 0.3, 1);
}
.menuicon-top.active {
  top: 24px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transition: top 0.25s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: top 0.25s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: top 0.25s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: top 0.25s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: top 0.25s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
}
.menuicon-bottom {
  display: block;
  position: absolute;
  bottom: 20px;
  width: 17px;
  height: 1px;
  background: #ffffffaa;
  border-radius: 0.5px;
  -webkit-border-radius: 0.5px;
  -moz-border-radius: 0.5px;
  -ms-border-radius: 0.5px;
  -o-border-radius: 0.5px;
  transition: bottom 0.5s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.25s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: bottom 0.5s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.25s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: bottom 0.5s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.25s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: bottom 0.5s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.25s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: bottom 0.5s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.25s cubic-bezier(0.5, 0, 0.3, 1);
}
.menuicon-bottom.active {
  bottom: 24px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transition: bottom 0.25s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -webkit-transition: bottom 0.25s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -moz-transition: bottom 0.25s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -ms-transition: bottom 0.25s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
  -o-transition: bottom 0.25s cubic-bezier(0.5, 0, 0.3, 1),
    transform 0.5s cubic-bezier(0.5, 0, 0.3, 1);
}
/* === 版本標示 === */
.version {
  justify-content: left !important;
  padding-left: 15px;
}
.version span {
  font-size: 15px;
  color: #5c5c5c;
  white-space: nowrap;
  cursor: pointer;
}

/* === Navigation bar ==================================================== */
footer {
  z-index: 1000;
  display: flex;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  height: 60px;
  width: 100%;
  background: #000;
  box-shadow: 0 -5px 5px 0px #0f0f0f;
}
#footer {
  display: flex;
  align-items: center;
  height: 60px;
  width: 100%;
}
#footer-ul {
  display: flex;
  justify-content: space-around;
  height: 100%;
  width: 100%;
  padding: 0 22px;
}
#footer-li {
  list-style: none;
  height: 100%;
  width: 25%;
  background: #000;
  border-radius: 0;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  -o-border-radius: 0;
}
#footer-li div {
  font-size: 20px;
  color: #acacac;
}
#footer-li div span {
  display: none;
}
.li-view {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100% - 22px);
  width: 100%;
  border-radius: 10px 10px 17px 17px;
  -webkit-border-radius: 10px 10px 17px 17px;
  -moz-border-radius: 10px 10px 17px 17px;
  -ms-border-radius: 10px 10px 17px 17px;
  -o-border-radius: 10px 10px 17px 17px;
}
.footerBoxShadow {
  box-shadow: 0 -20px 20px -20px inset #3e71c2;
}
.li-borderRadius {
  z-index: 0;
  position: relative;
  margin-bottom: -10px;
  top: 0;
  left: -10px;
  height: 10px;
  width: calc(100% + 20px);
  background: #0f0f0f;
}
.li-circle-L {
  z-index: 0;
  position: relative;
  margin-bottom: -20px;
  top: 0;
  left: -20px;
  height: 20px;
  width: 20px;
  border-radius: 10px;
  background: #000000;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.li-circle-R {
  z-index: 0;
  position: relative;
  margin-bottom: -20px;
  top: 0;
  left: 100%;
  height: 20px;
  width: 20px;
  border-radius: 10px;
  background: #000000;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

/* === Home ==================================================== */

#home-view {
  display: flex;
  justify-content: center;
  width: 100%;
}
#home-view-content {
  width: 90%;
}
.home-block {
  margin: 3rem 0;
}

.home-post {
  display: flex;
  position: relative;
  width: 100%;
  flex-direction: column;
  justify-content: end;
  padding: 20px;
  margin: 10px 0;
  background-position: top;
  background-size: cover;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
.home-post > .space::after {
  z-index: 0;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.075),
    rgba(0, 0, 0, 0.15),
    rgba(0, 0, 0, 0.275),
    rgba(0, 0, 0, 0.45),
    rgba(0, 0, 0, 0.65),
    rgba(0, 0, 0, 0.8)
  );
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
.home-post h1 {
  z-index: 1;
  color: #ffffffd1;
}
.home-post span {
  z-index: 1;
  color: #ffffff8f;
}

/* === Post ==================================================== */

#post-view {
  display: flex;
  justify-content: center;
  width: 100%;
}
#post-view-content {
  width: 90%;
}
.post-block {
  margin: 3rem 0;
}
.post-block > h1 {
  font-size: 32px;
}

.post-post {
  display: flex;
  position: relative;
  width: 100%;
  flex-direction: column;
  justify-content: start;
  padding: 20px;
  margin: 10px 0;
  background-position: top;
  background-size: cover;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
.post-block div .space::after {
  z-index: 0;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.075),
    rgba(0, 0, 0, 0.15),
    rgba(0, 0, 0, 0.25),
    rgba(0, 0, 0, 0.35),
    rgba(0, 0, 0, 0.45),
    rgba(0, 0, 0, 0.6)
  );
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
.post-post h5 {
  z-index: 1;
  color: #0000005a;
}
.post-post h1 {
  z-index: 1;
  color: #ffffffd1;
}
.post-post span {
  z-index: 1;
  color: #ffffff8f;
}

/* === Folder ==================================================== */

#folder-view {
  display: flex;
  justify-content: center;
  width: 100%;
}
#folder-view-content {
  width: 90%;
}
.folder-block {
  margin: 3rem 0;
}
.folder-post {
  display: flex;
  position: relative;
  width: 100%;
  height: 150px;
  flex-direction: column;
  justify-content: end;
  padding: 20px;
  margin: 10px 0;
  background-position: top;
  background-size: cover;
  box-shadow: 0 5px 20px 0 #ffffff0a;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
}
.folder-post > .space::after {
  z-index: 0;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.1),
    rgba(0, 0, 0, 0.175),
    rgba(0, 0, 0, 0.35),
    rgba(0, 0, 0, 0.475),
    rgba(0, 0, 0, 0.55),
    rgba(0, 0, 0, 0.65),
    rgba(0, 0, 0, 0.8)
  );
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
.folder-post h1 {
  z-index: 1;
  color: #ffffffd1;
}
.folder-post span {
  z-index: 1;
  color: #ffffff9f;
}

/* === Development ==================================================== */

#development {
  width: 100%;
}
#development-view {
  display: flex;
  justify-content: center;
  width: 100%;
}
#development-view-content {
  width: 90%;
}
.development-block {
  margin: 10vw 0;
}
.development-post {
  display: flex;
  position: relative;
  width: 100%;
  flex-direction: column;
  justify-content: start;
  padding: 20px;
  margin: 10px 0;
  background-position: top;
  background-size: cover;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
.development-block > h1 {
  font-size: 32px;
}
.development-post > h3 {
  color: #6a6a6a;
}
.development-post h5 {
  z-index: 1;
  color: #0000005a;
  margin-bottom: 10px;
}
.development-post h1 {
  z-index: 1;
  color: #ffffffa1;
}
.development-post span {
  z-index: 1;
  margin: 0.1rem 0;
  color: #ffffff5f;
}
.bg-history h1 {
  color: #a1a1a1;
}
.bg-history h5 {
  color: #3a3a3a;
}
.bg-history span {
  color: #606060 !important;
}

#courseSchedule {
  width: 100%;
  padding-bottom: 2rem;
}
#CSView {
  height: auto;
  position: relative;
  padding: 1rem 1rem 2rem 1rem;
}
#CSView > h1 {
  margin: 1rem 0 0 1rem;
  color: #cfcfcf;
}
.leftMask {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 0.9rem;
  box-shadow: 0 0px 15px 10px #0f0f0f;
  z-index: 10;
}
.rightMask {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 0.9rem;
  box-shadow: 0 0px 15px 10px #0f0f0f;
  z-index: 10;
}

#TableDiv {
  display: flex;
  position: relative;
  overflow: auto;
  white-space: nowrap;
  padding-bottom: 3rem;
  padding-right: 15px;
  left: 0;
}

.Table_TitleRows {
  margin-left: 1rem;
  margin-right: 0.5rem;
}
.Table_TopTitle {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.8rem;
  padding: 0.5rem;
  color: #5f5f5f;
}
.Table_LeftTitle {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5px;
  margin-left: 0.5rem;
  height: 2.4rem;
  padding: 0.5rem;
  font-size: 0.8rem;
  color: #5f5f5f;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
.Table_LeftTitle.open {
  height: 4rem;
}

.Table_Rows {
  margin-right: 0.5rem;
}
.Table_Rows_bg {
  padding: 0 10px;
  border-radius: 1.2rem;
  background-color: #ffffff10;
  -webkit-border-radius: 1.2rem;
  -moz-border-radius: 1.2rem;
  -ms-border-radius: 1.2rem;
  -o-border-radius: 1.2rem;
}
.Table_Rows_bg .Table_TopTitle {
  color: var(--Table-Title-text-bg);
}

.Table_Data {
  display: flex;
  position: relative;
  flex-direction: column;
  /*justify-content: center;
  */
  align-items: center;
  cursor: pointer;
  height: 2.4rem;
  font-size: 0.75rem;
  color: #cfcfcf;
  padding: 0.5rem 0.7rem 0.5rem 0.7rem;
  margin-bottom: 5px;
  border-radius: 0.7rem;
  box-shadow: 0 3px 0 0 var(--Table-Data-BoxShadow);
  background-color: #1f1f1f;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
.Table_Data.open {
  color: #efefef;
  background-color: #262626;
  height: 4rem;
  margin-bottom: 5px;
  padding: 0.5rem 1rem 0.5rem 1rem;
}
.Table_Data span {
  color: #8a8a8a00;
  font-size: 0.7rem;
  font-weight: 200;
  letter-spacing: 0;
  line-height: 0.7;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.Table_Data span.open {
  display: block;
  color: #8a8a8a;
  letter-spacing: 2px;
  line-height: 1.2;
}
.Table_noData {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 1rem;
  color: #00000000;
  padding: 0.7rem;
  margin-bottom: 10px;
  border-radius: 0.7rem;
  box-shadow: 0 3px 0 0 #00000000;
  background-color: #00000000;
  transition: 0.2s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s;
  -webkit-border-radius: 0.7rem;
  -moz-border-radius: 0.7rem;
  -ms-border-radius: 0.7rem;
  -o-border-radius: 0.7rem;
}

.ClassChange {
  color: var(--Class-Change-text) !important;
  background-color: var(--Class-Change-bg) !important;
  box-shadow: 0 3px 0 0 var(--Class-Change-BoxShadow) !important;
}
.ClassChange:hover {
  background-color: var(--Class-Change-bg-hover) !important;
  box-shadow: 0 5px 0 0 var(--Class-Change-BoxShadow) !important;
}
